import axios from "axios"
import Qs from "qs"
const baseUrl = 'http://47.100.246.138:8000';

const http = axios.create({
	baseURL: baseUrl,
	transformRequest: [function(data) {
		data = Qs.stringify(data);
		return data;
	}],
});

initInterceptors(http);

function initInterceptors(axios) {
	//添加请求拦截器
	axios.interceptors.request.use(function(config) {
		// 在发送请求之前做些什么
		if(config.data) {
			console.log("请求参数:", config.data)
		}
		return config;
	}, function(error) {
		// 对请求错误做些什么
		console.log("请求发生有异常:", error)
		return Promise.reject(error);
	});
	// 添加响应拦截器
	axios.interceptors.response.use(function(response) {
		if(response.status == 200 || response.status == 304) {
			console.log("返回结果:", response.data)
			return response.data
		} else if(response.status == 500) {
			alert("服务器内部错误!")
		} else {
			// 对响应数据做点什么
			console.log("response结果:", response.data)
		}

		return response;
	}, function(error) {
		// 对响应错误做点什么
		console.log("返回发生异常:", error)
		return Promise.reject(error);
	});

}
export {
	http,
};