import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import index from './views/index.vue'
import login from './views/login.vue'

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			name: 'login',
			component: login,
		},
		{
			path: '/index',
			name: 'index',
			component: index,
			children: [{
					path: '/',
					name: "backIndex",
					component: resolve => require(['@/views/index/backIndex.vue'], resolve),
				},
				{
					path: '/anniu',
					name: "anniu",
					component: resolve => require(['@/views/component/anniu.vue'], resolve),
				},
				{
					path: '/jgts',
					name: "jgts",
					component: resolve => require(['@/views/component/jgts.vue'], resolve),
				},
				{
					path: '/kp',
					name: "kp",
					component: resolve => require(['@/views/component/kp.vue'], resolve),
				},
				{
					path: '/xttxan',
					name: "xttxan",
					component: resolve => require(['@/views/component/xttxan.vue'], resolve),
				},
				{
					path: '/DatePicker',
					name: "DatePicker",
					component: resolve => require(['@/views/component/DatePicker.vue'], resolve),
				},
				{
					path: '/countryPartner',
					name: "countryPartner",
					component: resolve => require(['@/views/component/countryPartner.vue'], resolve),
				},
				{
					path: '/districtAgent',
					name: "districtAgent",
					component: resolve => require(['@/views/component/districtAgent.vue'], resolve),
				},
				{
					path: '/business',
					name: "business",
					component: resolve => require(['@/views/component/business.vue'], resolve),
				},
			],
		},
	]
})
