			let a = this.array.pop()
			console.log('删除的最后一位元素', a, '删除后返回的数组', this.array)
			//shift 删除数组的第一位元素
			let b = this.array.shift();
			console.log('删除数组的第一位元素', b, '返回删除后的数组', this.array)
			//unshift 在数组的最前面增加元素
			let c = this.array.unshift(this.array1);
			console.log('增加数组的第一位元素', c, '返回增加元素后的数组', this.array)
			//reverse 颠倒数组元素的循序
			let d = this.array.reverse();
			console.log('reverse结果', d, 'array', this.array)
			//ES6: copyWithin() 指定位置的成员复制到其他位置 有三个参数 第一个开始的文字 第二个开始截图的位置 第三个 截取结束的位置
			let e = this.array.copyWithin(1, 2, 3);
			console.log('copyWithin结果', e);
			// ES6: fill() 填充数组定义: 使用给定值，填充一个数组。参数:第一个元素(必须): 要填充数组的值第二个元素(可选): 填充的开始位置,默认值为0第三个元素(可选)：填充的结束位置，默认是为this.length
			this.array.fill(1, 0, 1);
			console.log('fill结果', this.array)


			/* -------------------不改变原数组的方法(8个):--------------------------- */
			// silce  浅拷贝 接受两个参数
			let aa = this.array.slice(3, 5)
			console.log('silce', aa, 'array', this.array)
			//join() 数组转字符串,把数组中的所有元素通过指定的分隔符进行分隔放入一个字符串，返回生成的字符串。
			let bb = this.array2.join('~')
			console.log('silce', bb, 'array', this.array2)
			//concat 合并数组
