				当有多个请求时我们可以通过Promise.all来获取所有请求
				我们可以通过Promise.race来获取最初获取的内容
				Promise标准
				失败的统一用catch来管*/
				/* 异常捕获语法 */
        // async/await
				
				/* async/await用法
				 1，使用await,函数必须用async标识
				 2，await后面跟的是一个Promise的实例
				 3,需要兼容*/
				 function load(src){
					 const Promisea = new Promise(function(resolve,reject){
						 function a (){
							  if(src===1) resolve(src+100)
								if(src===11) reject('请求失败,请重新输入')
						 }
						
						 setTimeout(a,1)
					 })
					 return Promisea
				 }
			  	const loads = async function(){
					const result1 = await load(1)
					console.log(result1)
					const result2 = await load(11)
					console.log(result2)
				}
				loads()
				
