import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './util/base.css'
import echarts from 'echarts'
import store from './store/store'
import{ http }   from './server/http';
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import _ from 'lodash'
Vue.prototype._ = _
import BaiduMap from 'vue-baidu-map';
Vue.use(BaiduMap,{
  ak:"LN4nmRBvPfIt5zNfKedGWNySjf3HZqNM"
});

import axios from "axios"
// import Qs from 'qs'
// axios.defaults.baseURL = 'http://127.0.0.1:8080'
Vue.prototype.$http = axios
import '../mock/index' // mock 方式，正式发布时，注释掉该处即可
import '../mock/ditu' // mock 方式，正式发布时，注释掉该处即可
import '../mock/baojingA' // mock 方式，正式发布时，注释掉该处即可
import '../mock/baojingB' // mock 方式，正式发布时，注释掉该处即可
import '../mock/baojingc'
import '../mock/baojingd'

window.http = http;//請求
Vue.use(ElementUI);
Vue.config.productionTip = false
//must
import MuseUI from 'muse-ui';
import 'muse-ui/dist/muse-ui.css';
Vue.use(MuseUI);
const ToastOption = {
    position: 'top', // 弹出的位置
    time: 2000, // 显示的时长
    closeIcon: 'close', // 关闭的图标
    close: true, // 是否显示关闭按钮
    successIcon: 'check_circle', // 成功信息图标
    infoIcon: 'info', // 信息信息图标
    warningIcon: 'priority_high', // 提醒信息图标
    errorIcon: 'warning' // 错误信息图标
}
import Toast from 'muse-ui-toast';
Vue.use(Toast, ToastOption);

import Message from 'muse-ui-message';
import 'muse-ui-message/dist/muse-ui-message.css';
Vue.use(Message);
Vue.prototype.$echarts = echarts 
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
