import Mock from 'mockjs' //引入mockjs，npm已安装
import {
	Random
} from 'mockjs' // 引入random对象,随机生成数据的对象，（与占位符@一样）
Mock.setup({
	timeout: 1 //设置请求延时时间
})
const ditu = function(option) { //定义请求数据方法
	let datalist = []
	for (let i = 0; i < 17; i += 1) {
		const o = { //mockjs模拟随机生成数据，生成7条
			airMonitors: Random.string('number', 1), //数字
			companyid: Random.guid(12), //id号
			companyName: Random.cword(9, 15), // 随机生成任意名称
			controlLevel: Random.county(), //城市
			latitude: Mock.mock({
				'latitude|32.8': 32.01,
			}),
			longitude: Mock.mock({
				'longitude|120.6': 120.8,
			}),
			shortName:Random.cword(3, 6),
			status:Random.string('number', 1), //数字
			waterMonitors:Random.string('number', 1), //数字
		}
		datalist.push(o)
	}
	return {
		data: datalist
	}
}
Mock.mock('/ditu', /post|get/i, ditu) //调用模拟数据方法
