import Mock from 'mockjs' //引入mockjs，npm已安装
import {
	Random
} from 'mockjs' // 引入random对象,随机生成数据的对象，（与占位符@一样）
Mock.setup({
	timeout: 1 //设置请求延时时间
})
const baojingc = function(option) { //定义请求数据方法
	let datalist = []
	for (let i = 0; i < 11; i += 1) {
		const o = { //mockjs模拟随机生成数据，生成7条
			type: Random.string('number', 1), //数字
			monitorName: Random.cword(6, 10), // 随机生成任意名称
		}
		datalist.push(o)
	}
	return {
		data: datalist
	}
}
Mock.mock('/baojingc', /post|get/i, baojingc()) //调用模拟数据方法
