import Mock from 'mockjs' //引入mockjs，npm已安装
import {
	Random
} from 'mockjs' // 引入random对象,随机生成数据的对象，（与占位符@一样）
Mock.setup({
	timeout: 1 //设置请求延时时间
})
const getdata = function(option) { //定义请求数据方法
	let datalist = []
	let COD = []
	let andan = []
	let zonglin = []
	for (let i = 0; i < 9; i += 1) {
		// 		const o = { //mockjs模拟随机生成数据，生成20条
		// 			name: Random.boolean(), //布尔
		// 			recipeId: Random.guid(), //id号
		// 			billId: Random.string(5, 10), //符号
		// 			orgId: Random.string('number', 8, 10), //数字
		// 			Date: Random.date('yyyy-MM-dd'), //日期
		// 			time: Random.time('A HH:mm:ss'), //时间
		// 			adress: Random.county(), //城市
		// 			viewName: Random.cword(4, 10), // 随机生成任意名称
		// 			personName: Random.cname(), //人名字
		// 			reason: Random.csentence(10, 32), //文字
		// 		}
		const a = { //mockjs模拟随机生成数据，生成20条
			number: Random.string('number', 1), //数字
			name: Random.cword(4, 10), // 随机生成任意名称
		}
		COD.push(a)
	}
	for (let i = 0; i < 6; i += 1) {
		const b = { //mockjs模拟随机生成数据，生成20条
			number: Random.string('number', 1), //数字
			name: Random.cword(4, 10), // 随机生成任意名称
		}
		andan.push(b)
	}
	for (let i = 0; i < 6; i += 1) {
		const c = { //mockjs模拟随机生成数据，生成20条
			number: Random.string('number', 1), //数字
			name: Random.cword(4, 10), // 随机生成任意名称
		}
		zonglin.push(c)
	}
	datalist.push(COD)
	datalist.push(andan)
	datalist.push(zonglin)
	return {
		data: datalist
	}
}
Mock.mock('/user', /post|get/i, getdata) //调用模拟数据方法
