import Mock from 'mockjs' //引入mockjs，npm已安装
import {
	Random
} from 'mockjs' // 引入random对象,随机生成数据的对象，（与占位符@一样）
Mock.setup({
	timeout: 1 //设置请求延时时间
})
const baojingA = function(option) { //定义请求数据方法
	let datalist = []
	let data1 = []
	let data2 = []
	let data3 = []
	let data4 = []
	let time = []
	//1
	//2
	const lineChart ={
		data1,
		data2,
		data3,
		data4,
		time,
	}
	//3
	for (let i = 0; i < 7; i += 1) {
	  	data1.push(Random.string('number', 2))
	}
	//4
	for (let i = 0; i < 7; i += 1) {
		data2.push(Random.string('number', 2))
	}
	//5
	for (let i = 0; i < 7; i += 1) {
		data3.push(Random.string('number', 2))
	}
	//6
	for (let i = 0; i < 7; i += 1) {
		data4.push(Random.string('number', 2))
	}
   //8
   for (let i = 0; i < 7; i += 1) {
   	time.push(Random.date('yyyy-MM-dd'))
   }
   const o = { //mockjs模拟随机生成数据，生成7条
   	counts: Random.string('number', 3), //数字
   	lineChart,
   }
	datalist.push(o)
	return {
		data: datalist
	}
}
Mock.mock('/baojingA', /post|get/i, baojingA) //调用模拟数据方法
